    //   this.defaultCheckedKeys = this.vectorTreeData.map(node => node.id);
    // },

    //处理勾选变化
    handleCheckChange(data, checked){
      const item = this.vectorItems[data.id];
      if (item){
        item.visible = checked;
        this.toggleVectorVisibility(item);
      }
    },

    //切换矢量可视化
    toggleVectorVisibility(item){
      if(item.points){
        item.points.show = item.visible;
        // 确保每个点都同步显隐状态
      item.points._pointPrimitives.forEach(point => {
        point.show = item.visible;
      });
      }
      if(item.dataSource){
        item.dataSource.show = item.visible;
      }
      this.viewer.scene.requestRender(); // 关键：强制渲染
    },

    //更新矢量颜色
    updataVectorColor(data){
      const item = this.vectorItems[data.id];
      if(item){
        item.color = data.color;
        if (item.points){
          this.updatePointsColor(item.points, data.color);
        }
        if(item.dataSource){
          this.setDataSourceStyle(item.dataSource, data.color);
        }
      }
    },
    //更新点颜色
    updatePointsColor(points, color){
      const cesiumColor = Cesium.Color.fromCssColorString(color);
      points._pointPrimitives.forEach(point =>{
        point.color = cesiumColor;
      });
    },

    //清空数据
    clearData() {
          // 清空通过 loadTiffImage 加载的影像图层（保留底图）
          const imageryLayers = this.viewer.imageryLayers;
          for (let i = imageryLayers.length - 1; i >= 0; i--) {
              const layer = imageryLayers.get(i);
              // 检查是否为 SingleTileImageryProvider（通过 loadTiffImage 添加的图层）
              if (layer.imageryProvider instanceof Cesium.SingleTileImageryProvider) {
              imageryLayers.remove(layer);
              }
            }

        
        // 清理加载状态
        this.loadingProgress = 0;
        this.loadedChunks.clear();
        this.totalChunks = 0;
        this.showLoadingProgress = false;
        this.showAttributeLoadingProgress = false;
        this.attributeLoadingProgress = 0;
        this.attributeLoadingText = '';

        this.viewer.dataSources.removeAll();

        const primitives = this.viewer.scene.primitives
        for (let i = primitives.length -1 ; i>=0; i--){
          if (primitives.get(i) instanceof Cesium.PointPrimitiveCollection){
            primitives.remove(primitives.get(i))
          }
        }
        
          // 清理绘制的线条
        this.drawlines.forEach(line => this.viewer.entities.remove(line));
        this.drawlines = [];

          // 清理相交点数据
        this.intersectingPoints = [];


        
        // 清理图表数据
        this.pointAttributeChartData = [];
        this.clickedPoint = null;

          // 清理测距数据
        this.distancePoints = [];
        this.distanceResult = null;
        this.clearDistanceEntities();

        // 清理测面积数据
        this.areaPoints = [];
        this.areaResult = null;
        this.clearAreaEntities();

        // 清理选择状态
        this.selectedLayer = '';
        this.selectedAttribute = '';
        this.legendSelectedLayer = '';
        this.legendSelectedAttribute = '';
        this.legendDataRange = { min: 0, max: 0 };
        this.selectedPointLayer = '';
        this.selectedPointAttributes = '';

         // 清理图表实例
        if (this.largeChartInstance) {
          this.largeChartInstance.dispose();
          this.largeChartInstance = null;
        }

      // 清理tree和vectorItems
        this.vectorTreeData = [];
        this.vectorItems = {};
        this.defaultCheckedKeys = [];

      // 清理临时变量
        this.tempLine = null;
        this.drawLinePoints = [];
        this.isDrawingLine = false;



  // 清理各种处理器
        if (this.drawHandler) {
          this.drawHandler.destroy();
          this.drawHandler = null;
        }
        if (this.distanceHandler) {
          this.distanceHandler.destroy();
          this.distanceHandler = null;
        }
      if (this.areaHandler) {
          this.areaHandler.destroy();
          this.areaHandler = null;
        }
        if (this.pointClickHandler) {
          this.pointClickHandler.destroy();
          this.pointClickHandler = null;
        }
    // 重置各种状态
      this.isMeasuringDistance = false;
      this.isMeasuringArea = false;
      this.measureDistancePopoverVisible = false;
      this.measureAreaPopoverVisible = false;
      this.popoverVisible = false;
      this.showDialog = false;
      this.editDialogVisible = false;

  // 强制垃圾回收（如果可用）
  if (window.gc) {
    window.gc();
  }

    },
        // 加载 TIF 影像
    async loadTiffImage(imageData) {
      try {
        // const response = await fetch(url);
        // const data = await response.json();
        // const imageUrl = data.image;
        console.log(imageData)
        if (!imageData || !imageData.image || !imageData.bounds){
          throw new Error ('无效的影像数据格式')
        }

        const {image, bounds} = imageData;
        const { minLon, minLat, maxLon, maxLat} = bounds

        // 创建图片图层
        const imageryProvider = new Cesium.SingleTileImageryProvider({
          url: image,
          rectangle: Cesium.Rectangle.fromDegrees(minLon, minLat, maxLon, maxLat),
        });

        this.viewer.imageryLayers.addImageryProvider(imageryProvider);
        await this.viewer.camera.flyTo({
          destination: Cesium.Rectangle.fromDegrees(minLon,minLat,maxLon,maxLat),
          orientation:{
            heading: 0,
            pitch: -Cesium.Math.PI_OVER_TWO,
            roll: 0,
          }
        })
      } catch (e) {
        console.error("加载失败:", e);
      }
    },

    async handleSuccess(response) {

      if (response.vectorData) {
      for (let i = 0; i < response.vectorData.length; i++) {
        const metadata = response.vectorData[i];
        const name = response.name[i];
        await this.loadVectorDataWithPagination(metadata, name);
      }
    }

      //根据文件扩展名调用对应的方法
      if (response.tiffImages){
        for (const imageUrl of response.tiffImages){
            await this.loadTiffImage(imageUrl);
        }
      }
    },
    

    handleNodeClick(data, node){
      // 如果是色带或属性预览子节点，则触发对话框
      if (data.type === 'color-preview' || data.type === 'attribute-preview') {
        this.showEditDialog(data.parentData); // 传递父节点数据
        } else {
    // 默认行为（如展开/折叠）
        node.expanded = !node.expanded;
        }
    },

    updateColorForNode(parentNode, color){
      const id = parentNode.data.id.split('-')[0];
      const item = this.vectorItems[id];
      if (item){
        item.color = color;
        parentNode.data.color = color;
        // console.log(parentNode)
        this.updataVectorColor(item);
      }
    },

    getColorScale(minvalue, maxvalue){
      const colors = this.colorScheme[this.selectedColorScheme];
      return(value) =>{
        const ratio = (value - minvalue) / (maxvalue - minvalue);
        if(colors.length === 2){
          return Cesium.Color.fromCssColorString(colors[0]).lerp(
            Cesium.Color.fromCssColorString(colors[1]),
            ratio
          );
        }else{
          if(ratio <= 0.5){
            return Cesium.Color.fromCssColorString(colors[0]).lerp(
              Cesium.Color.fromCssColorString(colors[1]),
              ratio * 2
            );
          } else{
            return Cesium.Color.fromCssColorString(colors[1]).lerp(
              Cesium.Color.fromCssColorString(colors[2]),
              (ratio - 0.5) * 2
            );
          }
        }
      };
    },

    //切换色带
    changeColorScheme(Scheme){
      this.selectedColorScheme = Scheme;
      this.updatePointsColors();
    },

    //更新单个数据集的属性字段
    updateVectorItemAttribute(id, attribute){
      console.log('222')
      const item = this.vectorItems[id];
      if(item){
        item.selectedAttribute = attribute;
        this.updatePointsColorsDirectly(item);
      }
    },

    //更新单个数据集的色带
    updateVectorItemColorScheme(id, Scheme) {
      const item = this.vectorItems[id];
      if (item){
        item.selectedColorScheme = Scheme;
        this.updatePointsColorsDirectly(item);
      }
    },

    //获取当前数据集的可用属性字段
getAvailableAttributes(id){
  const item = this.vectorItems[id];
  if (!item) return [];
  
    if(Array.isArray(item.attributeKeys) && item.attributeKeys.length){
      return item.attributeKeys;
    }
  // 优先使用点数据的属性
  if (item.pointFeatures.length > 0) {
    const props = item.pointFeatures[0].properties || {};
    return Object.keys(props);
  
  }
  
  // 如果没有点数据，使用线面数据的属性
  if (item.dataSource && item.dataSource.entities.values.length > 0) {
    const firstEntity = item.dataSource.entities.values[0];
    if (firstEntity.properties) {
      const obj = firstEntity.properties.getValue()|| {};
      return Object.keys(obj);
    }
  }
  
  return [];
    },

  async fetchAttributeBatched(filePath, attributeName, pointIds, batchSize = 100000){
  const all = {};
  const totalBatches = Math.ceil(pointIds.length / batchSize);
  
  const loadingMessage = this.$message({
    message: `正在读取属性信息...(0/${totalBatches} 批)`,
    type: 'info',
    duration: 0, // 不自动消失
    showClose: false,
    offset:80
  })
  try{
  for (let i = 0; i < pointIds.length; i += batchSize) {
    const batchNum = Math.floor(i / batchSize) + 1;
    const slice = pointIds.slice(i, i + batchSize);

    try{
      const resp = await this.$http.post('/load-attribute-values', {
        filePath,
        attributeName,
        pointIds: slice
      });
      Object.assign(all, (resp.data && resp.data.values) || {});

      if(batchNum % 5 ===0 ){
        this.monitorMemory();
      }
      // 更新消息内容
      loadingMessage.message = `正在读取属性信息... (${batchNum}/${totalBatches} 批)`;
    }catch(e){
      // 简单重试一次
      try{
        const resp = await this.$http.post('/load-attribute-values', {
          filePath,
          attributeName,
          pointIds: slice
        });
        Object.assign(all, (resp.data && resp.data.values) || {});
      }catch(e2){
        console.warn('分批拉取失败批次:', i, e2);
      }
    }
  }
}finally{
  // 关闭loading消息并显示成功消息
  loadingMessage.close();
  this.$message.success('属性信息读取完成');
}


  // this.checkMemoryAfterLoad();

  return all;
},
  async updatePointsColorsDirectly(vectorItem) {
  if (!vectorItem.points|| !vectorItem.selectedAttribute) return;
  

  const statsResp = await this.$http.post('/get-attribute-stats',{
    filePath:vectorItem.metadata.filePath,
    attributeName:vectorItem.selectedAttribute
  });

  const stats = statsResp.data;
  console.log('stats:',stats);
  console.log('stats.min:',stats.min);
  console.log('stats.max:',stats.max);
  const colorScale = this.getColorScale(stats.min,stats.max,vectorItem.selectedColorScheme);
  console.log('colorScale:',colorScale);

  const needFetch = vectorItem.pointFeatures.some(f=>{
    const props = f.properties || {};
    const value = props[vectorItem.selectedAttribute];

    return value === undefined || value === null;
  });

  if (needFetch && vectorItem.pointFeatures.length > 0){

    const loadingMessage = this.$message({
    message: '正在读取属性信息...',
    type: 'info',
    duration: 0, // 不自动消失
    showClose: false,
    offset:80
  })
  
      const pointIds = vectorItem.pointFeatures.map(f =>  Number(f.id));
      const valuesMap = await this.fetchAttributeBatched(
        vectorItem.metadata.filePath,
        vectorItem.selectedAttribute,
        pointIds,
        1000
      );

    // 仅写回当前所选字段
    vectorItem.pointFeatures.forEach(f => {
      if (!f.properties) f.properties = {};
      const kNum = Number(f.id);
      const kStr = String(f.id);
      const val = valuesMap[kNum] !== undefined
        ? valuesMap[kNum]
        : valuesMap[kStr];
      if (val !== undefined) {
        f.properties[vectorItem.selectedAttribute] = val;
      }
    });
    loadingMessage.close();
  }

  vectorItem.points.removeAll();
  // console.log('Before map - selectedAttribute:', vectorItem.selectedAttribute);
  // console.log('First few features properties:', vectorItem.pointFeatures.slice(0, 3).map(f => ({
  //   id: f.id,
  //   props: f.properties,
  //   selectedValue: f.properties[vectorItem.selectedAttribute]
  // })));

  vectorItem.pointFeatures.forEach(feature => {
    const value = parseFloat(feature.properties[vectorItem.selectedAttribute]);
    console.log('color value:',value);
    console.log('colorscale:',colorScale(value));
    const color = isNaN(value) ? Cesium.Color.WHITE : colorScale(value);
    vectorItem.points.add({
      position: Cesium.Cartesian3.fromDegrees(...feature.geometry.coordinates),
      color,
      pixelSize: 5
    });
  });

},
getColorScale(minValue, maxValue, scheme) {
  // 清洗色带
  const key = (scheme || '').trim();
  console.log('色带key:', key);
  const raw = this.colorScheme && this.colorScheme[key];
  console.log('原始色带:', raw);
  const colors = Array.isArray(raw) ? raw.filter(c => typeof c === 'string' && c.trim()) : [];
  console.log('颜色数组:', colors);
  const fallback = '#e41a1c';
  const toCes = c => Cesium.Color.fromCssColorString((c || fallback));

  // 非法色带
  if (colors.length === 0) return () => toCes(fallback);
  // 单色
  if (colors.length === 1) {
    const single = toCes(colors[0]);
    return () => single;
  }

  // 非法范围或 min==max：用中间色
  const minv = Number(minValue), maxv = Number(maxValue);
  console.log('数值范围:', minv, '到', maxv);
  if (!isFinite(minv) || !isFinite(maxv) || minv === maxv) {
    const mid = toCes(colors[Math.min(Math.floor(colors.length / 2), colors.length - 1)]);
    console.log('范围异常，使用中间色:', mid);
    return () => mid;
  }

  // 渐变
  return (value) => {
    let v = Number(value);
    if (!isFinite(v)) v = minv; // 兜底

    // 关键：ratio 夹紧到 [0,1]
    let t = (v - minv) / (maxv - minv);
    if (!isFinite(t)) t = 0;
    t = Math.max(0, Math.min(1, t));

    console.log(`值: ${v}, 比例: ${t}, 色带长度: ${colors.length}`);

    if (colors.length === 2) {
      const c1 = toCes(colors[0]);
      const c2 = toCes(colors[1]);
      const result = Cesium.Color.lerp(c1, c2, t, new Cesium.Color());
      console.log('双色渐变结果:', result);
      return Cesium.Color.lerp(c1, c2, t, new Cesium.Color());
    }

    const seg = 1 / (colors.length - 1);
    const idx = Math.min(Math.max(Math.floor(t / seg), 0), colors.length - 2);
    const localT = (t - idx * seg) / seg;

    const c1 = toCes(colors[idx]);
    const c2 = toCes(colors[idx + 1]);
    const result = Cesium.Color.lerp(c1, c2, localT, new Cesium.Color());
    console.log(`多色渐变: 索引${idx}, 局部比例${localT}, 结果:`, result);
    return result;
  };
},

  //显示编辑对话框
  showEditDialog(nodeData){
    this.editingData={
      id:nodeData.id,
      selectedAttribute:nodeData.selectedAttribute,
      selectedColorScheme:nodeData.selectedColorScheme
    };
    if(!this.editingData.selectedAttribute){
      const opts = this.getAvailableAttributes(nodeData.id);
      if(opts.length) this.editingData.selectedAttribute = opts[0];
    }
    this.editDialogVisible = true;
  },

  //确定修改
  async confirmEdit(){
    const item = this.vectorItems[this.editingData.id];
    if(item){
      item.selectedAttribute = this.editingData.selectedAttribute;
      item.selectedColorScheme = this.editingData.selectedColorScheme;
      
      if(item.points){
        this.updatePointsColorsDirectly(item);
      }
      if(item.dataSource){
        this.updateDataSourceColors(item);
      }

      this.updateTreeData();

      //如果当前图例显示的是这个图层，则同步更新图例
      if(this.legendSelectedLayer === this.editingData.id){
        this.legendSelectedAttribute = this.editingData.selectedAttribute;
        this.updateLegendData();
      }

      //如果当前折线图显示的是这个图层，则更新图例
    if(this.selectedLayer === this.editingData.id){
      this.selectedAttribute = this.editingData.selectedAttribute;
      this.updateSmallChart();
    }
    }
    this.editDialogVisible = false;
  },

  //获取色带预览
  getPreviewStyle(schemeName){
    const colors = this.colorScheme[schemeName] || ['#ccc'];

    if(colors.length === 1){
      return{
        background: colors[0]
      };
    }
    return{
      background:colors.length > 1
      ? `linear-gradient(to right, ${colors.join(',')})`
      : colors[0]
    };
  },

 async updateDataSourceColors(vectorItem) {
    if (!vectorItem || !vectorItem.dataSource || !vectorItem.selectedAttribute) return;

  // 确保属性已就绪
  const hasSomeProps = vectorItem.pointFeatures.some(p => p.properties && vectorItem.selectedAttribute in p.properties);
  if (!hasSomeProps) {
    await this.loadPolygonLineAttribute(vectorItem);
  }

  // 安全获取色带与取色函数
  const schemeKey = (vectorItem.selectedColorScheme || '').trim();
  const rawPalette = this.colorScheme && this.colorScheme[schemeKey];
  const palette = Array.isArray(rawPalette) ? rawPalette.filter(c => typeof c === 'string' && c.trim()) : [];
  const firstColor = (palette.length > 0 ? palette[0] : '#e41a1c');
  const toCes = (c) => Cesium.Color.fromCssColorString((typeof c === 'string' && c.trim()) ? c : '#e41a1c').withAlpha(0.8);



  // 获取统计范围
  const statsResp = await this.$http.post('/get-attribute-stats', {
    filePath: vectorItem.metadata.filePath,
    attributeName: vectorItem.selectedAttribute
  });
  const stats = statsResp.data || {};

  if(stats.numeric === false){
    const solid = toCes(firstColor)
    vectorItem.dataSource.entities.values.forEach(ent =>{
      if(ent.polygon){
        ent.polygon.material = solid;
        ent.polygon.outlineColor = solid;
      }
      if(ent.polyline){
        ent.polyline.material = solid;
      }
    });
    this.viewer.scene.requestRender();
    return;
  }


  const minv = Number(stats.min);
  const maxv = Number(stats.max);
  if (!isFinite(minv) || !isFinite(maxv) || minv === maxv) {
    const solid = toCes(firstColor)
    vectorItem.dataSource.entities.values.forEach(ent =>{
      if(ent.polygon){
        ent.polygon.material = solid;
        ent.polygon.outlineColor = solid;
      }
      if(ent.polyline){
        ent.polyline.material = solid;
      }
    });
    this.viewer.scene.requestRender();
    return;
  }

  const colorScale = this.getColorScale(minv, maxv, vectorItem.selectedColorScheme);

// 为每个实体设置颜色
  vectorItem.dataSource.entities.values.forEach(ent => {
    let value = null;
    if (ent.properties && ent.properties[vectorItem.selectedAttribute]) {
      const v = ent.properties[vectorItem.selectedAttribute].getValue
        ? ent.properties[vectorItem.selectedAttribute].getValue(Cesium.JulianDate.now())
        : ent.properties[vectorItem.selectedAttribute];
      value = parseFloat(v);
    }
    const color = isFinite(value) ? colorScale(value) : Cesium.Color.WHITE;

    if (ent.polygon) {
      ent.polygon.material = color;
      ent.polygon.outlineColor = color;
    }
    if (ent.polyline) {
      ent.polyline.material = color;
    }
  });

  this.viewer.scene.requestRender();

},
  },
beforeDestroy(){
  if(this.drawHandler){
    this.drawHandler.destroy();
  }
  if(this.largeChartInstance){
    this.largeChartInstance.dispose();
    this.largeChartInstance = null;
  }
  if(this.dialogResizeObserver){
    this.dialogResizeObserver.disconnect();
    this.dialogResizeObserver = null;
  }
  if (this.memoryMonitorInterval) {
    clearInterval(this.memoryMonitorInterval);
  }
}
};



